import {GeneralPostQuery,saveTeacherTb,changeTeacherPsw} from './api'
const teacherInfo = {
  state:{
    teacherInfo:{userdata:null,list:[],selectedOptions:[],zrwList:[]},
  },
  mutations: {

    INIT_TEACHER_INFO: (state, payload) => {
      state.teacherInfo.userdata=payload
    },
  },
  actions: {

    //-------------------------------------------------------------------------- 工作录入页面Actions--------------------------------------------------------------------------------

    //学生申报信息表（只显示当前有效记录）（所有参数都是开关型）
    // param1:老師ID
    // param2:学校ID

    reqTeacherInfo:({commit},params)=>{
      return new Promise((resolve,reject) => {
        const parameter={
          sqlId:'S370007',
          limit:1,
          ...params
        }
        GeneralPostQuery(parameter)
            .then((res)=>{
              if(res.success){
                commit('INIT_TEACHER_INFO',res.data[0])
                resolve(res)
              }else{
                resolve(res)
              }
            })
      })
    },
        //保存教职工申报信息表
    // jsonData
    saveTeacherTb:({commit},params)=>{
      return new Promise((resolve,reject) => {
        const parameter={
          ...params
        }
        saveTeacherTb(parameter)
            .then((res)=>{
              if(res.success){
                resolve(res)
              }else{
                resolve(res)
              }
            })
      })
    },
        //修改老师密码
    // admin/KUser.editPwd.json
    // param1: $scope.service.editpwd.pwd1 //原密码
    // param2: $scope.service.editpwd.pwd2 //新密码
    // param3: $scope.service.editpwd.pwd3 //新密码重复

    changeTeacherPsw:({commit},params)=>{
      return new Promise((resolve,reject) => {
        const parameter={
          ...params
        }
        changeTeacherPsw(parameter)
            .then((res)=>{
              if(res.success){
                resolve(res)
              }else{
                resolve(res)
              }
            })
      })
    },



  },

  getters: {

  }
}

export default teacherInfo
